
import {wxJsConfig} from "@/api/wx";
import {initWxJSSDK} from 'wx-choose-pay'

const SHARE_TITLE = '';
const SHARE_DESC = '';
const SHARE_LINK = '';
const SHARE_IMG = '';

const state = {
};

const mutations = {
}

const actions = {
  // JS-SDK
  initWxJSSDK({}) {
    return new Promise((resolve,reject) => {
console.log(1);
    wxJsConfig().then(res => {

      console.log(2);
      const jsApiList = [
        'updateAppMessageShareData',
        'onMenuShareAppMessage',
      ];

      let config = {
        ...res.data,
        debug: true,
        jsApiList
      };
      initWxJSSDK(config).then(
        () => {
          
      console.log(3);
          resolve('wx JS-SDK !')
        },
        err => {
          reject(err);
        })
    })
    });
  },

  // 
  async getLocation({dispatch}){
    let res = await dispatch('initWxJSSDK').catch(err=>false);
    return res !== false ? await getLocation() : await getCurrentPosition();
  },

  // 
  updateShare({dispatch},data={}){
    
    console.log(0);
    return new Promise((resolve,reject) => {
      dispatch('initWxJSSDK').then(()=>{
        let {
          title = SHARE_TITLE, // 
          desc = SHARE_DESC,   // 
          link = SHARE_LINK,   // JS
          imgUrl = SHARE_IMG,  // 
        } = data;
        console.log(4);

        wx.onMenuShareAppMessage({
          title,
          desc,
          link,
          imgUrl,
          success: function () {
            console.log('');
            resolve();
            // 
          },
          fail(err) {
            reject();
            console.log('', err);
          }
        })
      },reject)
    });
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}

//  JS-SDK 
function getLocation() {
  return new Promise((resolve,reject) => {
    wx.getLocation({
      type: 'wgs84', // wgs84gpsopenLocation'gcj02'
      success: resolve,
      error: reject
    });
  });
}

//  geolocation api 
function getCurrentPosition() {
  return new Promise((resolve, reject) => {
    navigator && navigator.geolocation ?
      navigator.geolocation.getCurrentPosition(position => resolve(position.coords), reject) :
      reject()
  });
}



